import React, { useState } from "react";
import axios from "axios";
import Header from "./components/Header.jsx";
import First from "./components/First";
import Second from "./components/Second";
import Third from "./components/Third";
import Slider from "./components/Slider";
import Footer from "./components/Footer";
import Courses from "./components/Courses";
import Cart from "./components/Cart";
import "./styles/style.css";

export const AppContext = React.createContext({});

function App() {
  const [lessons, setLessons] = React.useState([]);
  const [cartLessons, setCartLessons] = React.useState([]);
  const [cartOpened, setCartOpened] = React.useState(false);

  React.useEffect(() => {
    async function fetchData() {
      const lessonsResponse = await axios.get(
        "https://66cb5ff34290b1c4f19a2c8e.mockapi.io/lessons"
      );
      const cartLessonsResponse = await axios.get(
        "https://66cb5ff34290b1c4f19a2c8e.mockapi.io/cartLessons"
      );
      setLessons(lessonsResponse.data);
      setCartLessons(cartLessonsResponse.data);
    }
    fetchData();
  }, []);

  const onAddToCart = async (lsn) => {
    await axios
      .post("https://66cb5ff34290b1c4f19a2c8e.mockapi.io/cartLessons", lsn)
      .then((res) => setCartLessons((prev) => [...prev, res.data]));
  };

  const onRemoveCart = (id) => {
    axios.delete(
      `https://66cb5ff34290b1c4f19a2c8e.mockapi.io/cartLessons/${id}`
    );
    setCartLessons((prev) => prev.filter((el) => el.key !== id));
  };

  const cleanCart = async () => {
    const response = await axios.get(
      "https://66cb5ff34290b1c4f19a2c8e.mockapi.io/cartLessons"
    );

    const cartItems = response.data;

    await Promise.all(
      cartItems.map((item) =>
        axios.delete(
          `https://66cb5ff34290b1c4f19a2c8e.mockapi.io/cartLessons/${item.id}`
        )
      )
    );

    setCartLessons([]);
  };

  const isItemAdded = (id) => {
    return cartLessons.some((obj) => Number(obj.id) === Number(id));
  };

  return (
    <AppContext.Provider value={{ lessons, cartLessons, isItemAdded }}>
      <div className="App">
        <Header onClickCart={() => setCartOpened(true)} />
        {cartOpened ? (
          <Cart
            items={cartLessons}
            onDelete={onRemoveCart}
            onClose={() => setCartOpened(false)}
            cleanCart={cleanCart}
          />
        ) : null}
        <First />
        <Second />
        <Third />
        <Slider />
        <div className="courses" id="courses">
          <div className="courses__title">Курсы</div>
          <div className="courses__wrapper">
            {lessons.map((el) => (
              <Courses
                id={el.id}
                added={isItemAdded(el.id)}
                addToCart={(lsn) => onAddToCart(lsn)}
                onClickCart={() => setCartOpened(true)}
                title={el.name}
                price={el.price}
              />
            ))}
          </div>
        </div>
        <Footer />
      </div>
    </AppContext.Provider>
  );
}

export default App;
