import React from "react";
import Form from "./Form copy.jsx";
import EmptyCart from "./EmptyCart.jsx";

const Cart = ({ onDelete, onClose, cleanCart, items = [] }) => {
  let price = 0;
  items.map((el) => {
    price += el.price;
  });

  let string = " ";
  items.map((el) => {
    string += el.title;
  });

  const [orderComplete, setOrderComplete] = React.useState(false);

  return (
    <div className="cart">
      <div className="overlay">
        <div className="cart-item">
          {items.length > 0 && (
            <h3>
              Корзина
              <img
                src="img/delete-cross-svgrepo-com.svg"
                alt=""
                className="cart-course__remove"
                onClick={onClose}
              />
            </h3>
          )}

          {items.length > 0 ? (
            <div className="cart__wrapper">
              {items.map((el) => (
                <div className="cart-course" id={el.id}>
                  <img
                    src="img/course1.jpg"
                    alt=""
                    className="cart-course__img"
                  />
                  <div className="cart-course__title corm_t">{el.title}</div>
                  <div className="cart-course__price corm_t">{el.price} ₽</div>
                  <img
                    src="img/delete-cross-svgrepo-com.svg"
                    alt=""
                    className="cart-course__remove"
                    onClick={() => {
                      onDelete(el.key);
                    }}
                  />
                </div>
              ))}
            </div>
          ) : (
            <EmptyCart
              title={orderComplete ? "Заказ оформлен!" : "Корзина пуста"}
              text={
                orderComplete
                  ? "Спасибо за покупку"
                  : "Добавьте хотя бы один курс, чтобы сделать заказ"
              }
              onClose={onClose}
            />
          )}

          {price ? (
            <Form
              cleanCart={() => {
                cleanCart();
                setOrderComplete(true);
              }}
              string={string}
              price={price}
            />
          ) : null}
        </div>
      </div>
    </div>
  );
};

export default Cart;
