import React, { useState } from "react";
import { AppContext } from "../App";

const Courses = ({
  title,
  price,
  addToCart,
  onClickCart,
  id,
  added = false,
}) => {
  const { isItemAdded } = React.useContext(AppContext);
  const [isButtonDisabled, setIsButtonDisabled] = React.useState(false);

  const onClickAdd = async () => {
    setIsButtonDisabled(true);
    await addToCart({ title, price, id });
    setIsButtonDisabled(false);
  };

  return (
    <div className="courses-item">
      <img src="img/course1.jpg" alt="" className="courses-item__img" />
      <div className="courses-item__title">{title}</div>
      <div className="courses-item__price corm_t">{price} ₽</div>
      <button
        className={
          isItemAdded(id)
            ? "courses-item__btn courses-item__btn_added"
            : "courses-item__btn"
        }
        onClick={isItemAdded(id) || isButtonDisabled ? onClickCart : onClickAdd}
        disabled={isButtonDisabled}
      >
        {isItemAdded(id) ? "В корзине" : "Купить"}
      </button>
    </div>
  );
};

export default Courses;
